:root{
    --primary: #155799;
    --secondary: #0c97b0;
    --dark: #0e5b12;
    --light: #eee;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Verdana", sans-serif;

}

body{
    background-color: var(--light);
}

.App{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    padding: 16px;
}

.calculator{
    width: 150;
    max-width: 300px;
    background-color: #FFF;
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.1);
}

.display{
    padding: 16px;
    text-align: right;
    background-color: var(--dark);
    background-image: linear-gradient(100deg, #000000, #155957);
    color:var(--light);
    font-size: 24px;
    font-weight: 300;

}

.display span{
    font-size: 14px;
    color: #888;
}

button{
    appearance: none;
    border: none;
    outline: none;
    color: #FFF;
    font-size: 20px;
    padding: 16px;
    cursor: pointer;
    transition: 0.4s;
}

button:hover {
    opacity: 0.9;
}

.operators {
    display: flex;
}
.operators button {
    flex: 1 1 0%;
    background-color: var(--primary);
    background-image: linear-gradient(100deg, #000000, #155957);
    font-weight: 700;
}

.digits {
    display: flex;
    flex-wrap: wrap;

}

    .digits button {
        flex: 1 1 33.333%;
        max-width: 33.333%;
        background-color: var(--dark);
        background-image: linear-gradient(100deg, #000000, #155957);
    }